<template>
    <div>
        <div class="container mt-3">

            <div class="mt-3" >
                <h3 v-if="entradaSalida.entrada"><i class="fas fa-truck-moving" ></i> Nueva Entrada</h3>
                <h3 v-if="!entradaSalida.entrada"><i class="fas fa-truck-moving"></i> Nueva Salida</h3>
            </div>
            <hr>

            <!-- Planta y Nota de remisión -->
            <div class="row mt-3">
                <div class="form-group col-sm-12 col-md-6">
                    <label>Planta</label>
                    <div class="input-group">
                        <b-form-select v-model="entradaSalida.codPlanta"
                                        id="cboPlantas"
                                        :options="plantas">
                        </b-form-select>
                    </div>
                </div>
                
                <div class="form-group col-sm-12 col-md-6">
                    <label>Nota Remisión</label>
                    <div class="input-group">
                        <select v-model="notaRemision"
                                class="form-control"
                                v-on:change="obtenerDatosNotaRemision">
                            <option v-for="nota in notas " 
                                    v-bind:key="nota.codigo"
                                    v-bind:value="nota"
                                    >  {{nota.text}}
                            </option>
                        </select>
                    </div>
                </div>
            </div>

            <!-- Equipo y Operador -->
            <div class="row">
                <div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2">
                    <label>Equipo</label>
                    <input class="form-control" type="text" readonly v-model="notaRemision.equipo">
                </div>
                
                <div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6">
                    <label>Operador</label>
                    <input class="form-control" type="text" readonly v-model="notaRemision.operador">
                </div>
            </div>

            <!-- Kilometraje y Horometraje -->
            <div class="row">
                <div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2">
                    <label>Kilometraje</label>
                    <div class="input-group">
                        <input v-model="notaRemision.ultimoKilometraje"
                            v-selectonfocus
                            @focus="onFocus(1)"
                            @keypress="numerosDecimales"
                            type="text"
                            class="form-control"
                            placeholder="Capture el kilometraje de la unidad."/>
                    </div>
                </div>
                
                <div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6">
                    <label>Horometraje</label>
                    <div class="input-group">
                        <input v-model="notaRemision.ultimoHorometraje"
                            v-selectonfocus
                            @focus="onFocus(2)"
                            @keypress="numerosDecimales"
                            type="text"
                            class="form-control"
                            placeholder="Capture el horometraje de la unidad."/>
                    </div>
                </div>
            </div>

            <!-- Observación -->
            <div class="row">
                <div class="form-group col-sm-12 col-md-6">
                <label>Observación</label>
                <div class="input-group">
                    <input v-model="entradaSalida.observacion"
                            v-selectonfocus
                            type="text"
                            class="form-control"
                            placeholder="Capture la observación."/>
                </div>
                </div>
            </div>

            <!-- botones -->
            <b-row class="my-3">
                <b-col class="col-md-6 col-sm-12 col-12" align-self="end"></b-col>
                <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
                    <b-button @click="GuardarEntradasSalidas" block variant="success" class="mr-2 mt-2">
                        <i class="fa fa-save"></i> Guardar
                    </b-button>
                </b-col>
                <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
                    <b-button @click="CancelarEntradaSalida" block variant="danger" class="mr-2 mt-2">
                        Cancelar
                    </b-button>
                </b-col>
            </b-row>
        </div>
    </div>
</template>

<script>

import Swal from 'sweetalert2'
import servicioPorteros from '@/services/servicio-porteros';
import servicioCobranza from "@/services/servicio-cobranza";
import servicioPlanta from "@/services/servicio-planta";

export default {
    data(){
        return {
            notas: [
                {
                    value: 0, 
                    text: "Seleccione una nota de remisión", 
                    codOperador: 0,
                    codEquipo: 0,
                    operador: '',
                    equipo: '',
                    idNotasRemisionEnc: 0,
                    ultimoKm:0,
                    ultimoHrm:0
                }
            ],
            notaRemision: [],
            inputSeleccionado: 0,
            entradaSalida: {
                ultimoKilometraje: 0,
                ultimoHorometraje: 0,
                observacion: '',
                entrada: false,
                notaRemision: 0,
                codEquipo: 0,
                CodOperador: 0,
                idNotasRemisionEnc: 0,
                codPlanta: 0,
                bacha:0
            },

            selected: 0,
            options: [
                { item: 0, name: 'Realizar Tiro' },
                { item: 1, name: 'Cambio de Planta' },
            ],
            plantas: [{ value: 0, text: "Seleccione una planta" }],
        }
    },
    components: {
    },
    mounted() {
        this.obtenerNotasRemision();
        this.obtenerPlantas();
    },
    created() {
        this.entradaSalida.entrada = this.$route.params.entrada == 1 ? true : false;
    },
    computed: {
    },
    methods: {

        async obtenerNotasRemision() {
            this.$loading(true);
            await servicioCobranza.obtenerNotasRemisionEntradasSalidas(this.entradaSalida.entrada)
                    .then((r) => {
                        if (r.value) {
                            r.data.forEach((element) => {
                                let tipo = { value: element.folio, text: element.folio + ' - ' + element.cliente, codEquipo: element.codEquipo, codOperador: element.codOperador, equipo: element.equipo, operador:  element.operador, idNotasRemisionEnc: element.idNotasRemisionEnc, ultimoKilometraje: element.ultimoKilometraje, ultimoHorometraje: element.ultimoHorometraje, bombeable: element.bombeable };
                                this.notas.push(tipo);
                            });
                            this.notaRemision = this.notas[0];
                        }
                    });
                    this.$loading(false);
        },

        async GuardarEntradasSalidas() {

            let tipo ="";
            if(this.entradaSalida.entrada) {
                tipo = "entrada";
            } else {
                tipo = "salida"
            }
            
            console.log( this.entradaSalida);
            let r = await Swal.fire({
                title: "¡ATENCIÓN!",
                text: "¿Desea guardar esta "+tipo+"?",
                showCancelButton: true,
                icon: 'question',
                confirmButtonColor: "#3085d6",
                cancelButtonColor: "#d33",
                confirmButtonText: "Aceptar",
                cancelButtonText: "Cancelar",
            });

            if (r.value) {
                this.entradaSalida.kilometraje = this.notaRemision.ultimoKilometraje;
                this.entradaSalida.horometraje = this.notaRemision.ultimoHorometraje;
                if(this.entradaSalida.codPlanta > 0) {
                    if(this.notaRemision.codEquipo > 0) {
                        if(this.notaRemision.codOperador > 0) {
                            if(this.notaRemision.value > 0) {
                                
                                if(this.entradaSalida.horometraje <= 0 || this.entradaSalida.kilometraje <= 0) {
                                    if(this.entradaSalida.kilometraje <= 0) {
                                        Swal.fire(
                                        '¡ATENCIÓN!',
                                        'Favor de ingresar correctamente el kilometraje.',
                                        'info'
                                        );

                                    } else {
                                        Swal.fire(
                                        '¡ATENCIÓN!',
                                        'Favor de ingresar correctamente el horometraje.',
                                        'info'
                                        );
                                    }
                                    
                                } else {

                                    await this.ValidacionDeDatos();

                                    this.$loading(true);
                                    servicioPorteros.guardarEntradasSalidas(this.entradaSalida)
                                                    .then( r => {
                                                        if(r.value) {
                                                            Swal.fire(
                                                                '¡EXITO!',
                                                                'Se guardo correctamente la ' + tipo,
                                                                'success'
                                                            );
                                                            // this.Inicializar();
                                                            this.$router.push({ path: "/portero/entradas-salidas" });
                                                        }
                                                        else {
                                                            Swal.fire(
                                                                '¡ATENCIÓN!',
                                                                r.message,
                                                                'error'
                                                            );
                                                        }
                                                        this.$loading(false);
                                                    }).catch(error=>{
                                                        console.log(error); 
                                                        this.$loading(false);
                                                    });
                                }

                            } else {
                                this.$swal.fire({
                                    text: `Favor de seleccionar una nota de remisión.`,
                                    icon: "warning"
                                });
                            }

                            
                        } else {
                            this.$swal.fire({
                                text: `Favor de seleccionar un operador.`,
                                icon: "warning"
                            });

                        }

                    } else {
                        this.$swal.fire({
                            text: `Favor de seleccionar un equipo.`,
                            icon: "warning"
                        });
                    }

                } else {
                    this.$swal.fire({
                        text: `Favor de seleccionar una planta.`,
                        icon: "warning"
                    });
                }

            }

        },

        async CancelarEntradaSalida() {
            this.$router.push({ path: "/portero/entradas-salidas/" })
        },

        numerosDecimales ($event) {
            let keyCode = ($event.keyCode ? $event.keyCode : $event.which);

            if(this.inputSeleccionado == 1) {
                if ((keyCode < 48 || keyCode > 57) && (keyCode !== 46 || this.entradaSalida.kilometraje.indexOf('.') != -1)) { // 46 is dot
                    $event.preventDefault();
                }
            } else if(this.inputSeleccionado == 2) {
                if ((keyCode < 48 || keyCode > 57) && (keyCode !== 46 || this.entradaSalida.horometraje.indexOf('.') != -1)) { // 46 is dot
                    $event.preventDefault();
                }
            } 

        },
        
        onFocus: function (index){
            this.inputSeleccionado = index;
        },
        
        ValidacionDeDatos() {

            if(this.entradaSalida.observacion == "") {
                this.entradaSalida.observacion = ""
            }

            if(this.entradaSalida.kilometraje == ""){
                this.entradaSalida.kilometraje = 0;
            }

            if(this.entradaSalida.horometraje == ""){
                this.entradaSalida.horometraje = 0;
            }
            this.entradaSalida.notaRemision = this.notaRemision.value;
            this.entradaSalida.codEquipo = this.notaRemision.codEquipo;
            this.entradaSalida.codOperador = this.notaRemision.codOperador;
            this.entradaSalida.idNotasRemisionEnc = this.notaRemision.idNotasRemisionEnc;
            
        },

        async obtenerDatosNotaRemision() {
            if(this.notaRemision.bombeable){
                let r = await this.$swal.fire({
                    // title: "¡ATENCIÓN!",
                    text: "¿Fue utilizada la Bacha?",
                    icon: "question",
                    showCancelButton: true,
                    confirmButtonColor: "#3085d6",
                    cancelButtonColor: "#d33",
                    confirmButtonText: "Si",
                    cancelButtonText: "No",
                 });

                if(r.value){
                    let cantidadBacha;

                        const { value: formValues } = await this.$swal.fire({
                            title: 'Ingrese la cantidad de bacha utilizada:',
                            html:
                                '<input id="swal-input2" class="swal2-input" placeholder="Ingrese la cantidad">',
                            focusConfirm: false,
                            showCancelButton: true,
                            confirmButtonText: 'Aceptar',
                            cancelButtonText: 'Cancelar',
                            confirmButtonColor: '#3085d6',
                            cancelButtonColor: '#d33',
                            preConfirm: () => {
                                cantidadBacha = parseInt(this.$swal.getPopup().querySelector('#swal-input2').value)    
                                let inputCantidad = this.$swal.getPopup().querySelector('#swal-input2');
                                inputCantidad.style.borderColor = "";

                                if (cantidadBacha === '' || isNaN(cantidadBacha)) {
                                    inputCantidad.style.borderColor = "red";
                                    Swal.showValidationMessage(`Favor de ingresar una cantidad valida.`)
                                }
                            },

                        })
                        if (formValues) {
                            this.entradaSalida.bacha = cantidadBacha;
                        }   
                }

            }
        },

        IsNumeric(eve){
            console.log("asd" +eve);
        },
        // plantas
        async obtenerPlantas() {
            this.$loading(true);
            await servicioPlanta.obtenerPlanta(0).then((r) => {
                r.data.forEach((el) => {
                    let planta = {
                        value: el.codigo,
                        text: el.descripcion,
                    };
                    this.plantas.push(planta);
                });
            });
        },
        
    }
}
</script>

<style scoped>

    

</style>
